<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <div id="app">
        <nav class="navbar navbar-expand-lg navbar-light bb-0 bg-white px-3"
             v-if="!isLoggedIn || !currentPage.includes('/doc/')">
            <a class="navbar-brand" href="/">Minion Events</a>
            <ul class="navbar-nav ml-auto flex-sm-row flex-column">
                <li class="nav-item" :class="[currentPage=='/'?'active':'']">
                    <router-link class="nav-link" to="/">Home</router-link>
                </li>
                <li class="nav-item" :class="[currentPage=='/login'?'active':'']">
                    <router-link class="nav-link" to="/login" v-if="!isLoggedIn">Login</router-link>
                </li>
                <li class="nav-item" :class="[currentPage=='/register'?'active':'']">
                    <router-link class="nav-link" to="/register" v-if="!isLoggedIn">Register</router-link>
                </li>
                <li class="nav-item" v-if="isLoggedIn" style="cursor: pointer">
                    <a class="nav-link" @click.prevent="logout">Logout</a>
                </li>
                <li class="nav-item" v-if="isLoggedIn" :class="[currentPage==='/events'?'active':'']">
                    <router-link class="nav-link" to="/events">View Events</router-link>
                </li>
                <li class="nav-item" v-if="isLoggedIn">
                    <a class="nav-link dropdown-toggle" data-toggle="dropdown" href="#" @click="showNotifications">
                        <span class="badge badge-success badge-pill" v-show="notifications.length > 0">{{notifications.length}}</span>
                        Notifications
                    </a>
                    <transition name="fade" :duration="200">

                        <ul class="dropdown-menu notify-drop" v-if="is_visible">
                            <div class="notify-drop-title">
                                <div class="row">
                                    <div class="col-md-6 col-sm-6 col-xs-6">Notifications
                                        (<b>{{notifications.length}}</b>)
                                    </div>
                                    <div class="col-md-6 col-sm-6 col-xs-6 text-right">
                                    <span @click="hide" class="rIcon allRead"
                                          data-tooltip="tooltip"
                                          title="Close">
                                        <i class="fa fa-close"></i>
                                    </span>
                                    </div>
                                </div>
                            </div>
                            <!-- end notify title -->
                            <!-- notify content -->
                            <div class="drop-content">
                                <li v-for="notification in notifications" :key="notification.id">
                                    <div class="col-md-9 col-sm-9 col-xs-9 pd-l0 ml-3 py-2">
                                        <i>{{notification.message}}</i>


                                    </div>
                                </li>

                            </div>
                            <div class="notify-drop-footer text-center">
                                <a href="#"><i class="fa fa-eye"></i>You have {{notifications.length}} unread
                                    notifications</a>
                            </div>
                        </ul>
                    </transition>

                </li>

            </ul>
        </nav>
        <router-view/>
    </div>
</template>

<script>
    import Echo from "laravel-echo";

    export default {
        name: 'App',
        components: {},
        created() {
            this.$store.dispatch('events/fetchNotifications', {});
            window.Echo = new Echo({
                broadcaster: 'pusher',
                key: "8cbe849dd186fe39a1b4",
                cluster: "ap2",
                encrypted: true,
                authEndpoint: this.$http.defaults.baseURL + '/broadcasting/auth',
                auth: {
                    headers: {
                        Authorization: localStorage.getItem('token'),
                        Accept: 'application/json',
                    },
                }

            });


        },

        beforeMount() {


            let channel = 'presence-online';

            window.Echo.private('presence-events')
                .listen('NewNotification', data => this.$store.commit("events/add_notification", {notification: data.notification}));


            window.Echo.join(channel) //Should be Channel Name
                .here((users) => {
                    // console.table(users);

                    // remove current user from current viewers
                    let userData = localStorage.getItem('user') || "{}";
                    let user = JSON.parse(userData);

                    this.$store.commit('docs/current_viewers', users);
                    this.$store.commit('docs/remove_current_viewer', user);

                })
                .joining((user) => {
                    console.table(user);
                    this.$store.commit('docs/add_current_viewer', user);

                })
                .leaving((user) => {
                    console.table(user);
                    this.$store.commit('docs/remove_current_viewer', user);


                });

        },
        beforeRouteEnter(to, from, next) {

            next((vm) => {
                window.Echo = new Echo({
                    broadcaster: 'pusher',
                    key: "8cbe849dd186fe39a1b4",
                    cluster: "ap2",
                    encrypted: true,
                    authEndpoint: vm.$http.defaults.baseURL + '/broadcasting/auth',
                    auth: {
                        headers: {
                            Authorization: localStorage.getItem('token'),
                            Accept: 'application/json',
                        },
                    }

                });

            });

        },

        data() {
            return {

                is_visible: false,

            }
        },
        computed: {
            isLoggedIn: function () {
                return this.$store.getters['auth/isLoggedIn'];
            },
            currentPage() {
                return this.$route.path;
            },
            notifications() {
                return this.$store.getters['events/notifications'];

            }
        },
        methods: {
            logout: function () {
                this.$store.dispatch("logout").then(() => {
                    this.$router.push("/login");
                });
            },
            showNotifications: function () {
                this.is_visible = true;
            },
            hide() {
                console.log("hide");
                this.is_visible = false;
            }
        }
    }
</script>

