<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <div id="app">
        <Layout>
            <template v-slot:main v-if="currentEvent">

                <div class="container">
                    <div class="row">
                        <div class="col-11 col-md-7 mx-auto mt-2">

                            <div class="card">
                                <div class="card-header ">
                                    <h2 class="card-title">
                                        {{currentEvent.title}}
                                    </h2>
                                    <div class="row mb-2 mx-0 px-0">
                                        <span class="card-subtitle">Participants : {{currentEvent.participants_count}}</span>
                                    </div>
                                    <div class="row mb-2 mx-0 px-0">
                                        <p class="card-subtitle d-block w-100"><b>Starts At</b> :
                                            {{currentEvent.starts_at | moment("ddd, hA") }}</p>
                                        <p class="card-subtitle d-block w-100 mt-1"><b>Ends At</b> :
                                            {{currentEvent.ends_at | moment("ddd, hA")}}</p>
                                    </div>
                                </div>
                                <div class="card-body">
                                    <p>{{currentEvent.description}}</p>
                                </div>
                                <div class="card-footer">
                                    <div class="row mx-0 px-0 mb-1 py-2">
                                        <div class="d-flex align-items-center justify-content-center mr-1">


                                        </div>
                                        <button
                                                class=" btn btn-default border mr-2"
                                                @click="likeEvent">

                                            <i class=""
                                               v-if="currentEvent.likes_count > 0"
                                               style="font-size: 1.45rem;color: black;">
                                                {{currentEvent.likes_count}}
                                            </i>
                                            <i class="fa fa-thumbs-o-up fa-2x like-btn text-muted"></i>
                                        </button>

                                        <button
                                                class=" btn btn-default border mr-4"
                                                @click="unlikeEvent">

                                            <i class=""
                                               v-if="currentEvent.dislikes_count > 0"
                                               style="font-size: 1.45rem;color: black;">
                                                {{currentEvent.dislikes_count}}
                                            </i>
                                            <i class="fa fa-thumbs-o-down fa-2x like-btn text-muted"></i>
                                        </button>
                                    </div>
                                    <div class="row mx-0 px-0">
                                        <button class="btn btn-danger btn-raised" v-if="isOwner" @click="deleteEvent">
                                            Delete
                                        </button>
                                        <button class="btn btn-primary btn-raised ml-2"
                                                v-if="!isOwner && !hasParticipated" @click="joinEvent">
                                            Join Event
                                        </button>
                                        <button class="btn btn-warning btn-raised ml-2"
                                                v-if="!isOwner && hasParticipated" @click="leaveEvent">
                                            Leave Event
                                        </button>
                                    </div>
                                </div>
                            </div>


                        </div>
                    </div>

                    <div class="row"
                         v-if="currentEvent.participants!=undefined && currentEvent.participants.length > 0">
                        <div class="col-md-7 col-11 mx-auto mt-4">
                            <h5>Participants</h5>
                            <div class=" border-bottom bg-white card" aria-labelledby=""
                                 v-for="user in currentEvent.participants"
                                 :key="user.email">
                                <div class="row mx-0 py-2">
                                    <div class="col-2 col-offset-1">
                                        <img
                                                :src="'https://ui-avatars.com/api/?name='+user.name.replace(/ /g,'+')+'&size=50'"
                                                alt="" class="rounded-circle user-img-thumb">
                                    </div>
                                    <div class="col-7">
                                        <span class="user-name d-block">{{user.name}}</span>
                                        <a :href="'mailto:'+user.email" class="user-email">{{user.email}}</a>
                                        <span class="d-block text-muted">{{ timeSinceNow(user.pivot.updated_at)
                                            }}</span>
                                    </div>
                                    <div class="col-2 d-flex align-items-center" v-if="isOwner">
                                        <button class=" mx-0 btn btn-danger btn-raised"
                                                @click="removeParticipant(user.id)">
                                            Remove
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </template>

        </Layout>
    </div>
</template>

<style scoped>
    .user-img-thumb {
        border: 2px solid indianred;
        cursor: pointer;
    }

    .user-img-thumb:hover {
        border: 2px solid #fa6a6a;
        -webkit-box-shadow: -4px 6px 8px -5px #917F5E;
        -moz-box-shadow: -4px 6px 8px -5px #917F5E;
        box-shadow: -4px 6px 8px -5px #917F5E;
    }

    .user-details {
        background-color: #fff;
        border-radius: 4px;
        box-shadow: 0 1px 2px 0 rgba(60, 64, 67, .3), 0 2px 6px 2px rgba(60, 64, 67, .15);
        opacity: 1;
        position: absolute;
        width: 340px;
        z-index: 2201;
        left: -212px;
        margin-top: 0.5rem;
    }

    .user-name {
        font-size: 18px;
        line-height: 24px;
    }

    .user-email {
        color: #3c4043;
        cursor: pointer;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

    }

    .like-btn:hover {
        cursor: pointer;
        font-weight: 500 !important;
        color: black !important;
    }
</style>

<script>
    import Layout from './Layout.vue'
    import Echo from "laravel-echo";
    import utility from '../utilities';

    export default {
        title: "Realtime Events | ",
        name: 'Event',
        components: {
            Layout,
        },
        data: function () {
            return {}
        },

        created() {
            this.$store.dispatch('events/fetchEvent', {id: this.$route.params.id});
            this.$store.dispatch('events/fetchHasParticipated', {id: this.$route.params.id});

            window.Echo = new Echo({
                broadcaster: 'pusher',
                key: "8cbe849dd186fe39a1b4",
                cluster: "ap2",
                encrypted: true,
                authEndpoint: this.$http.defaults.baseURL + '/broadcasting/auth',
                auth: {
                    headers: {
                        Authorization: localStorage.getItem('token'),
                        Accept: 'application/json',
                    },
                }

            });

        },
        beforeMount() {


        },

        beforeRouteEnter(to, from, next) {
            next(vm => {
                vm.$store.dispatch('events/fetchEvent', {id: vm.$route.params.id});
                vm.$store.dispatch('events/fetchHasParticipated', {id: vm.$route.params.id});

            })

        },
        watch: {},
        computed: {

            isOwner() {
                let user = JSON.parse(localStorage.getItem('user') || "{}");

                if (user !== undefined && this.currentEvent !== undefined && this.currentEvent.owner !== undefined)
                    return (user.id == this.currentEvent.owner.id);
                return false;

            },
            currentEvent() {
                return this.$store.getters['events/currentEvent'];
            },
            hasParticipated() {
                return this.$store.getters['events/hasParticipated'];
            },


        },
        methods: {
            deleteEvent: function () {

                this.$store.dispatch('events/deleteEvent', {id: this.$route.params.id}).then(() => {

                    this.$router.push({name: 'events'});

                });

            },
            joinEvent: function () {

                this.$store.dispatch('events/joinEvent', {id: this.$route.params.id}).then(() => {

                    this.$store.dispatch('events/fetchEvent', {id: this.$route.params.id});
                    this.$store.dispatch('events/fetchHasParticipated', {id: this.$route.params.id});

                });

            },
            leaveEvent: function () {

                this.$store.dispatch('events/leaveEvent', {id: this.$route.params.id}).then(() => {

                    this.$store.dispatch('events/fetchEvent', {id: this.$route.params.id});
                    this.$store.dispatch('events/fetchHasParticipated', {id: this.$route.params.id});

                });

            },
            removeParticipant: function (userId) {

                this.$store.dispatch('events/removeParticipant', {
                    id: this.$route.params.id,
                    userId: userId
                }).then(() => {

                    this.$store.dispatch('events/fetchEvent', {id: this.$route.params.id});


                });

            },
            timeSinceNow(time) {
                return utility.timeSince(time);

            },
            likeEvent() {
                this.$store.dispatch('events/likeEvent', {id: this.$route.params.id}).then(() => {

                    this.$store.dispatch('events/fetchEvent', {id: this.$route.params.id});
                    // this.$store.dispatch('events/fetchHasParticipated', {id: this.$route.params.id});

                });

            },
            unlikeEvent() {
                this.$store.dispatch('events/unlikeEvent', {id: this.$route.params.id}).then(() => {

                    this.$store.dispatch('events/fetchEvent', {id: this.$route.params.id});
                    // this.$store.dispatch('events/fetchHasParticipated', {id: this.$route.params.id});

                });
            }

        }
    }
</script>

<style lang="scss">

</style>

<style>
    #app {

    }
</style>
