const getters = {
    isLoggedIn: state => !!state.token,
    createdEvents: state => state.createdEvents,
    participatedEvents: state => state.participatedEvents,
    allEvents: state => state.allEvents,
    showViewers: state => state.showViewers,
    currentViewers: state => state.currentViewers,
    currentEvent: state => state.currentEvent,
    reRenderKey: state => state.reRenderKey,
    isAuthorized: state => state.isAuthorized,
    showShareDialog: state => state.showShareDialog,
    notifications: state => state.notifications,
    hasParticipated: state => state.hasParticipated,

};


export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}
