<template>

    <div class="container">

        <div class="mt-2" v-if="createdEvents.length<=0">
            <div class="card-header">
                <h6> You have not created any event yet</h6>
            </div>
        </div>

        <div class="row px-0">
            <div class="col-11 mx-auto mt-2 px-0">

                <div class="row px-0 mx-0">
                    <router-link tag="div" to="/event/create"
                                 class="card px-3 py-5 col-md-2 col-sm-6 mb-1 doc-card doc-create-card mr-2">

                        <span class="mx-auto my-auto text-dark">Create event</span>
                        <span class="fa fa-plus fa-2x mx-auto my-auto"></span>
                    </router-link>

                    <div class="card col-md-2 col-sm-6 doc-card mr-1 mb-1" v-for="event in createdEvents"
                         :key="event.id">

                        <router-link :to="'/events/'+ event.id" tag="div" class="card-body d-flex">
                            <p class="mx-auto my-auto">{{event.title}}</p>

                        </router-link>
                    </div>
                </div>

                <hr>

                <div class="row mt-2 mb-2 mx-0 px-0" v-if="participatedEvents.length > 0">
                    <h5 class="">Recently Joined</h5>
                </div>

                <div class="row px-0 mx-0 mt-3">

                    <div class="card col-md-2 col-sm-6 doc-card mr-1 mb-1" v-for="event in participatedEvents"
                         :key="'joined_'+event.id">

                        <router-link :to="'/events/'+ event.id" tag="div" class="card-body d-flex">
                            <p class="mx-auto my-auto">{{event.title}}</p>

                        </router-link>
                    </div>
                </div>

                <hr>

                <div class="row mt-2 mb-2 mx-0 px-0" v-if="allEvents.length > 0">
                    <h5 class="">All Events</h5>
                </div>

                <div class="row px-0 mx-0 mt-3">

                    <div class="card col-md-2 col-sm-6 doc-card mr-1 mb-1" v-for="event in allEvents"
                         :key="'joined_'+event.id">

                        <router-link :to="'/events/'+ event.id" tag="div" class="card-body d-flex">
                            <p class="mx-auto my-auto">{{event.title}}</p>

                        </router-link>
                    </div>
                </div>


            </div>
        </div>


    </div>
</template>

<style scoped>
    .doc-card {
        height: 200px;
        cursor: pointer;
    }

    .doc-create-card {
        opacity: 0.9;
    }
</style>
<script>
    export default {
        title: "Realtime Events",
        name: 'Events',
        data() {
            return {
                error: ""
            };
        },
        beforeMount() {
            this.fetchCreatedEvents();
            this.fetchParticipatedEvents();
            this.fetchAllEvents();
        },

        beforeRouteEnter(to, from, next) {
            next(vm => {
                vm.fetchCreatedEvents();
                vm.fetchParticipatedEvents();
                vm.fetchAllEvents();
            })

        },
        watch: {},
        computed: {

            createdEvents() {
                return this.$store.getters['events/createdEvents'] || [];
            },
            participatedEvents() {
                return this.$store.getters['events/participatedEvents'] || [];
            },
            allEvents() {
                return this.$store.getters['events/allEvents'] || [];
            }

        },
        methods: {
            fetchCreatedEvents: function () {
                this.$store
                    .dispatch("events/fetchCreatedEvents")
                    .then(() => {
                        console.log("Fetched Created Events");

                    })
                    .catch((err) => {
                        console.log(err);
                        if (err != undefined && !err.response.success)
                            this.error = err.response.data.error;
                    });
            },
            fetchParticipatedEvents: function () {
                this.$store
                    .dispatch("events/fetchParticipatedEvents")
                    .then(() => {
                        console.log("Fetched Participated Events");

                    })
                    .catch((err) => {
                        console.log(err);
                        if (err != undefined && !err.response.success)
                            this.error = err.response.data.error;
                    });
            },
            fetchAllEvents: function () {
                this.$store
                    .dispatch("events/fetchAllEvents")
                    .then(() => {
                        console.log("Fetched all Events");

                    })
                    .catch((err) => {
                        console.log(err);
                        if (err != undefined && !err.response.success)
                            this.error = err.response.data.error;
                    });
            }
        }
    };
</script>
