<template>

    <div class="container ">
        <div class="row">
            <div class="col-8 bg-white card mt-2 mx-auto px-0">
                <div class="card-header mx-0 px-0">
                    <h3 class="card-title mx-auto text-center">Create Event</h3>
                </div>
                <div class="card-body container">
                    <div class="row">
                        <div class="col-10 mx-auto">
                            <form @submit.prevent="createDoc">
                                <div class="alert alert-danger" v-show="error.length > 0">{{error}}</div>
                                <div class="form-group">
                                    <label for="title" class="bmd-label-floating">Title</label>
                                    <input id="title" class="form-control" required v-model="title" type="text">
                                </div>
                                <div class="form-group">
                                    <label for="data" class="bmd-label-floating">Description</label>
                                    <textarea rows="=5" class="form-control" id="data"
                                              v-model="description">
                                    </textarea>
                                </div>
                                <div class="form-group">
                                    <label for="startsAt" class="bmd-label-floating">Starts At</label>
                                    <Datetime input-class="form-contro" input-id="startsAt" v-model="startsAt"
                                              type="datetime" :use12-hour="true"
                                              :min-datetime="minStartDatetime"></Datetime>
                                </div>
                                <div class="form-group">
                                    <label for="endsAt" class="bmd-label-floating">Ends At</label>
                                    <Datetime input-class="form-contro" input-id="endsAt" v-model="endsAt"
                                              type="datetime" :use12-hour="true"
                                              :min-datetime="minEndDatetime"></Datetime>
                                </div>
                                <button type="submit" class="btn btn-raised btn-primary">Create</button>
                            </form>
                        </div>
                    </div>

                </div>


            </div>
        </div>


    </div>
</template>
<script>
    import {Datetime} from 'vue-datetime';

    export default {
        components: {
            Datetime
        },
        data() {
            return {
                title: "",
                startsAt: "",
                endsAt: "",
                description: "",
                error: ""
            };
        },
        watch: {

            title() {
                this.error = "";
            },
            data() {
                this.error = "";
            }


        },
        computed: {
            minStartDatetime() {
                return (new Date().toISOString());
            }
            ,
            minEndDatetime() {
                return (new Date().toISOString());
            }
        },
        methods: {
            createDoc: function () {
                this.$store
                    .dispatch("events/create", {
                        title: this.title,
                        description: this.description,
                        startsAt: this.startsAt,
                        endsAt: this.endsAt
                    })
                    .then((res) => {
                        console.log(res);
                        this.$router.push({name: 'event', params: {id: res.data.id}});
                        console.log("Create event");

                    })
                    .catch((err) => {
                        console.log(err);
                        if (err != undefined && !err.response.success)
                            this.error = err.response.data.error;
                    });
            }
        }
    };
</script>
